<template>
  <div id="container" class="wrapper">
  </div>
</template>
<script>
import 'gitalk/dist/gitalk.css'
import Gitalk from 'gitalk'
export default {
  mounted() {
    var id = this.id
    var gitalk = new Gitalk({
      clientID: 'd6bede35b03c7fe342cd',
      clientSecret: '27886c787d06b73b751f4a71cb08ca132938742a',
      repo: 'baiyan',
      owner: 'obaiyan',
      admin: ['obaiyan'],
      id: id,
      distractionFreeMode: false,
      language: 'zh-CN'
    })
    gitalk.render('container')
  },
  props: {
    id: String
  }
}
</script>
<style lang="less">
@import url(../../assets/_variables.less);
.wrapper{
    .gt-link{
        color: @color-accent;
        border-bottom: 1px dotted @color-accent;
    }
    .gt-meta{
        border-bottom: 1px dotted @color-accent;
    }
    .gt-header-textarea:hover{
        background-color: @color-normal-x;
        color: @color-sub;
    }
    .gt-header-textarea {
        background-color: @color-sub;
        color: @color-normal-x;
    }
    .gt-container .gt-header-preview{
        background-color: @color-sub;
        color: @color-normal;
    }
    .gt-header-preview .markdown-body {
        background-color: @color-sub;
        color: @color-normal;
    }
    .gt-btn{
        border-color: @color-accent;
    }
    .gt-btn-public,.gt-btn-login{
        border: 1px solid @color-accent;
        background-color: @color-accent !important;
    }
    .gt-comment-content{
        background-color: @color-sub !important;
    }
    .gt-container .gt-comment-body{
        color: @color-normal-x !important;
    }
    .gt-btn-preview{
        background-color: @color-normal-xx;
    }
    .gt-comment-username,.gt-btn-preview,.gt-ico-text{
        color: @color-accent;
    }
    .gt-svg svg{
        fill: @color-accent;
    }

}
</style>



